@media screen and (max-width: 1023px) {
    
    $cols2: (col-mob-1: 8.333%, col-mob-2: 16.667%, col-mob-3: 25%, col-mob-4: 33.3%, col-mob-5: 41.7, col-mob-6: 50%, col-mob-7: 58.33%, col-mob-8: 66.67%, col-mob-9: 75%, col-mob-10: 83.33%, col-mob-11: 91.66%, col-mob-12: 100%);

@each $w, $val in $cols2 {
    .#{$w} {
        width: $val;
    }
}

    .menu-wrapper {
        @include dfc;
        position: relative;
        justify-content: center;
        align-items: center;
    }

    .menu-btn-wr {
        display: block;
        overflow: hidden;
        cursor: pointer;
        margin: 0;
        padding: 0;
        font-size: setFont(0);
        width: 22px;
        height: 22px;
        border: 2px solid white;
        border-radius:  3px;
        position: relative;

        .menu-btn {
            display: block;
            position: absolute;
            background-color: white;
            font-size: 0;
            height: 2px;
            width: 18px;
            top: 10px;
            left: 2px;
            right: 2px;
            transition: all 0s 0.3s;
            -o-transition: all 0s 0.3s;
            -moz-transition: all 0s 0.3s;
            -webkit-transition: all 0s 0.3s;
        }

        span::before,
        .menu-btn::after {
            position: absolute;
            display: block;
            height: 2px;
            width: 100%;
            background-color: white;
            content: '';
            transition-duration: 0.3s, 0.3s;  
            -o-transition-duration: 0.3s, 0.3s;  
            -moz-transition-duration: 0.3s, 0.3s;  
            -webkit-transition-duration: 0.3s, 0.3s;  
            transition-delay: 0.3s, 0s;
            -o-transition-delay: 0.3s, 0s;
            -moz-transition-delay: 0.3s, 0s;
            -webkit-transition-delay: 0.3s, 0s;
        }

        span.close {
            background: none;
        }

        span.close::before {
            top: 0;
            rotate: 45deg;
          }
        
        span.close::after {
            bottom: 0;
            rotate: -45deg;
        }
        
        span::after {
            bottom: -6px;
        }
    
        span::before {
            top: -6px;
        }
    }

    .dd-menu-cont {
        width: 150px;
        opacity: 0;
        visibility: hidden;
        position: absolute;
        top: 70px;
        color: transparent;
        transition: all, 0.5s;
        -o-transition: all, 0.5s;
        -moz-transition: all, 0.5s;
        -webkit-transition: all, 0.5s;
        z-index: 0;

        &>ul {
            @include dfc;
        }

        &>ul>a {
            width: 100%;
            border: 2px solid transparent;
        }

        ul>a>li {
            margin: 20px 0;
            font-size: setFont(1.5);
        }
    }

    .dd-menu-cont.show {
        opacity: 1;
        visibility: visible;
        z-index: 999;
        background-color: rgb(50, 50, 50);
        color: rgba(0,0,0,1);
        top: 50px;
      }

      /*Приветсвие*/
      .greets {
        .content{
            h1{
                font-size: setFont(4);
            }
            h2 {
                font-size: setFont(4);
            }
            p {
                font-size: setFont(3);
            }
        }
      }

      /*Почему Вам нужен собственный сайт?*/
      .rmore {
        .rmore-header {
            text-align: center;
            font-size: setFont(3);
        }
        .rmore-txt {
            .rmore-element {
                margin: 20px 0 20px 0;
                img {
                    border-width: 2px;
                    padding: 4px;
                }
                h4 {
                    font-size: setFont(1.5);
                    word-wrap: break-word;
                }
                p {
                    font-size: setFont(1.3);
                    padding: 0 10px 0 10px;
                }
            }
        }
    }

    .about-wrapper {
        .txt-wr {
            h2 {
                font-size: setFont(3);
            }
            .aboutsite {
                h3 {
                    font-size: setFont(2);
                }
                .cathead {
                    font-size: setFont(1.5);
                }
            }
            .content-text {
                p, h4{
                    font-size: setFont(1.5);
                    font-weight: normal;
                }
            }
        }
        
    }

    /*Стек*/
    .tech {
        .tech-header {
            font-size: setFont(4);
        }
        .Tech-txt {
            .main-tech {
                .Tech-element {
                    a {
                        img {
                            height: 100px;
                            width: 100px;
                        }
                    }
                    .card-head {
                        font-size: setFont(2.5);
                    }
                }
            }
            .base-tech {
                text-align: center;
                a {
                    img {
                        height: 40px;
                    }
                }
                h4 {
                    font-size: setFont(1.7);
                    text-align: center;
                }
            }
        }
    }

    .portf-wr {
        .text{
            h2 {
                font-size: 40px;
            }
            h3 {
                font-size: 20px;
            }
        }
        
    }

    .contacts-wr {
        .txt-wr {
            h3 {
                font-size: 40px;
            }
            p {
                font-size: 20px;
            }
            .social-wr {
                @include dfc;

                .social {
                    position: relative;
                    margin-bottom: 20px;
                }
            }
        }
    }
    
    .footer {
        @include dfc;
    }
}

