$blackcolor: #333333;
$redcolor: #ff9999;
$txtcolor: white;
$font_s: 20px;

@function setFont ($f) {
    @return $f*10px;
}

body, html {
    margin: 0;
    padding: 0;
    background-color: $blackcolor;
}

$cols: (col-1: 8.333%, col-2: 16.667%, col-3: 25%, col-4: 33.3%, col-5: 41.7, col-6: 50%, col-7: 58.33%, col-8: 66.67%, col-9: 75%, col-10: 83.33%, col-11: 91.66%, col-12: 100%);

@each $w, $val in $cols {
    .#{$w} {
        width: $val;
    }
}

@mixin dfr {
    display: flex;
    flex-direction: row;
    -ms-flex-direction: row;
    flex-wrap: wrap;
    -ms-flex-wrap: wrap;
}

@mixin dfc {
    display: flex;
    flex-direction: column;
    -ms-flex-direction: column;
}

@mixin h2_style {
    font-size: setFont(6);
        text-align: center;
        color: $redcolor;
        font-family: "lobster";
        font-weight: 400;
}

@mixin bgRlg ($grad) {
    background-image: repeating-linear-gradient($grad);
    background-image: -o-repeating-linear-gradient($grad);
    background-image: -moz-repeating-linear-gradient($grad);
    background-image: -webkit-repeating-linear-gradient($grad);
}

* {
    user-select: none;
    margin: 0;
    padding: 0;
}

.text {
    color: $txtcolor;
}

.wrapper {
    width: 100%;
}

/*шапка сайта*/

.site-header {
    @include dfr;
    position: sticky;
    height: 50px;
    justify-content: space-between;
    z-index: 999;
    background-color: $blackcolor;
}

.slideouted-menu {
    top: 0px;
    animation-name: slidein;
    -o-animation-name: slidein;
    -moz-animation-name: slidein;
    -webkit-animation-name: slidein;
    animation-duration: 0.5s;
    -o-animation-duration: 0.5s;
    -moz-animation-duration: 0.5s;
    -webkit-animation-duration: 0.5s;
    animation-fill-mode: forwards;
    -o-animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
  }
  
  .sticky-menu {
    top: -100px;
    animation-name: slideout;
    -o-animation-name: slideout;
    -moz-animation-name: slideout;
    -webkit-animation-name: slideout;
    animation-duration: 0.5s;
    -o-animation-duration: 0.5s;
    -moz-animation-duration: 0.5s;
    -webkit-animation-duration: 0.5s;
    animation-fill-mode: forwards;
    -o-animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
  }
  
  @keyframes slidein {
    from {
      top: 0px;
    }
    to {
      top: -100px;
    }
  }
  
  @keyframes slideout {
    from{
      top: -100px;
    }
  
    to {
      top: 0;
    }
  }

.menu-wrapper {
    margin-right: 50px;
}

.menu-wrapper ul {
    @include dfr;
    list-style: none;
    height: 100%;
    margin: 0;
    justify-content: space-between;

    li {
        font-size: setFont(2.5);
        width: 100%;
        text-align: center;
    }

    a {
        border-right: 2px solid $redcolor;
        width: 130px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
        border-bottom: 2px solid transparent;
        transition: border 0.2s;
        box-sizing: border-box;
    }

    

    a:hover,
    .active {
        border-bottom: 2px solid $redcolor;
    }

    a:last-child {
        border-right: none;
    }
}

/*Размытый фон*/
.cover {
    position: absolute;
    top: -15%;
    left: -15%;
    background-image: inherit;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: center;
    width: 130%;
    height: 130%;
    filter: blur(7px);
    box-shadow: 0 0 0 3000px inset rgba(0, 0, 0, 0.7);
    z-index: 2;
    overflow: hidden;
}

/*Секции*/
.sec {
    position: relative;
    min-height: 100vh;
    @include dfc;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    z-index: 1;

    .content {
        position: relative;
        z-index: 3;
    }
}

/*Приветсвие*/
.greets {
    text-align: center;
    .content {
        color: $redcolor;
    }
    h1 {
        font-family: "lobster";
        font-size: setFont(6);
        font-weight: normal;
    }
    h2{
        font-size: setFont(5.5);
        border-bottom: 3px solid $redcolor;
        font-family: "Alegreya Sans Medium";
        font-weight: 200;
    }
    p {
        font-size: setFont(5.5);
        font-family: "Cormorant Garamond Light";
        font-weight: 800;
    }
    p:first-child {
        margin-top: 40px;
    }
}

/*Почему Вам нужен собственный сайт?*/
 .rmore {
    h2 {
        @include h2_style();
    }
    .rmore-txt {
        color: $txtcolor;
        @include dfr;
        justify-content: center;
        align-items: flex-start;
        
        .rmore-element {
            text-align: center;
            margin: 30px 0 30px 0;
            img {
                width: 33%;
                border: 5px solid $redcolor;
                padding: 10px;
                border-radius: 50%;
            }
            h4 {
                font-size: setFont(4);
                font-family: "Pattaya";
                font-weight: normal;
            }
            p{
                font-size: setFont(3);
                font-family: "Roboto";
            }            
        }
    }
 }

 /*цены*/
 .about-wrapper {
    .txt-wr {
        color: $txtcolor;
        h2 {
            @include h2_style();
        }
        .aboutsite {
            h3 {
                text-align: center;
                font-size: setFont(4.5);
                color: $redcolor;
                font-family: "El Messiri";
                font-weight: normal;
            }
            .cathead {
                @include dfr;
                justify-content: space-between;
                font-size: setFont(3.5);
                color: $redcolor;
                font-family: "Roboto";
            }
            .list-elements {
                @include dfr;
                justify-content: space-between;

                h4 {
                    cursor: pointer;
                }
            }
            .underline {
                text-decoration: underline;
            }
            .content-text {
                font-size: setFont(2.5);
                font-family: "Roboto";
                text-align: justify;
            }
            .slider-wr {
                overflow: hidden;
            }
            .slide {
                @include dfr;
                flex-wrap: nowrap;
                -ms-flex-wrap: nowrap;
                transition: all 1s;
                .type-descr {
                    min-width: 100%;
                    margin-right: 1%;
                    opacity: 0;
                    transition: all 1s;
                }
                .show-txt {
                    opacity: 1;
                }
            }
        }
    }
 }

 /*Стек*/
 .tech {
    h2 {
        @include h2_style();
    }
    .Tech-txt {
        .main-tech {
            @include dfr;
            text-align: center;
            justify-content: center;

            .Tech-element {
                margin: 25px 0 50px 0;
                img {
                    height: 150px;
                    width: 150px;
                }
                &:nth-child(2)>a>img,
                &:nth-child(4)>a>img{
                    border-radius: 50%;
                    background-color: $txtcolor;
                }
                h4 {
                    color: $txtcolor;
                    font-size: setFont(3);
                    font-family: "lobster";
                    font-weight: normal;
                }
            }
        }
        .base-tech {
            @include dfr;
            justify-content: center;
            text-align: center;
            a {
                width:100%;
                img {
                    height: 70px;
                }
            }
            h4 {
                font-size: setFont(2);
                color: $txtcolor;
                font-family: "lobster";
                font-weight: normal;
            }
        }
    }
 }

/*Портфолио*/
.portf-wr{
    @include dfc;
    text-align: center;
    justify-content: space-between;

    h2 {
        font-family: "lobster";
        color: #ff9999;
        font-size: 50px;
    }

    h3 {
        font-family: 'Roboto';
        font-size: 26px;
        font-weight: normal;
    }

    .construct {
        $lingrad: 45deg, yellow, yellow 20px,  black 20px, black 40px;
        @include bgRlg ($lingrad);
        min-height: 20px;
        width: 100%;
    }
}

/*контакты*/

.contacts-wr {
    color: white;
    width: 100%;
    flex-wrap: wrap;
    -ms-flex-wrap: wrap;
    text-align: center;

    h3 {
        font-size: 50px;
        font-family: 'lobster';
        color: #ff9999;
    }

    p {
        font-family: "Roboto";
        font-size: 26px;
        font-weight: normal;
    }

    .social-wr {
        margin-top: 25px;
        position: relative;
        @include dfr;
        justify-content: space-evenly;

        .social {
            @include dfc;
            position: relative;
            justify-content: center;
            align-items: center;

            a{
                img{
                    height: 10vh;
                }
            }
            p{
                font-family: 'Roboto';
                position: absolute;
                bottom: -20px;
                font-size: 18px;
            }
        }

        .popup {
            display: inline;
            position: absolute;
            visibility: visible;
            width: 145px;
            opacity: 1;
            transition: opacity 0.2s;
            background-color: rgb(255, 246, 234);
            color: #000;

            span {
                font-size: 16px;
            }
        }

        .hidden {
            visibility: hidden;
            opacity: 0;
            font-size: 0;
        }
    }
}

.footer {
    text-align: center;
    color: white;
    font-size: 20px;
}

 @import "mobscreen";
 @import "lgscreen"