    /*Большие экраны*/
    @media screen and (min-width: 2560px) {
        .site-header {
            height: 100px;
        }

        /*шапка сайта*/
        .menu-wrapper {
            ul>a {
                &>li {
                    font-size: setFont(4);
                }
                width: 250px;
            }
        }
        /*Приветствие*/
        .greets {
            .content{
                h1{
                    font-size: setFont(8);
                }
                h2 {
                    font-size: setFont(7);
                }
                p {
                    font-size: setFont(6);
                }
            }
          }

        /*Почему Вам нужен собственный сайт?*/
        .rmore {
            .rmore-header {
                font-size: setFont(8);
            }
            .rmore-txt {
                .rmore-element {
                    h4 {
                        font-size: setFont(7);
                    }
                    p {
                        font-size: setFont(4);
                    }
                }
            }
        }

        /*Цены*/
        .about-wrapper {
            .txt-wr {
                h2 {
                    font-size: setFont(8);
                }
                .aboutsite {
                    h3 {
                        font-size: setFont(6);
                    }
                    .cathead {
                        font-size: setFont(5.5);
                    }
                }
                .content-text {
                    p, h4{
                        font-size: setFont(4);
                        font-weight: normal;
                    }
                }
            }
            
        }
    
    /*Стек*/
    .tech {
        .tech-header {
            font-size: setFont(8);
        }
        .Tech-txt {
            .main-tech {
                .Tech-element {
                    a {
                        img {
                            height: 250px;
                            width: 250px;
                        }
                    }
                    .card-head {
                        font-size: setFont(5);
                    }
                }
            }
            .base-tech {
                text-align: center;
                a {
                    img {
                        height: 100px;
                        width: 100px;
                    }
                }
                h4 {
                    font-size: setFont(3);
                    text-align: center;
                }
            }
        }
    }
}