/*шапка сайта*/
.site-header {
  display: flex;
  flex-direction: row;
  -ms-flex-direction: row;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  position: sticky;
  height: 50px;
  justify-content: space-between;
  z-index: 999;
  background-color: #333333;
}

.slideouted-menu {
  top: 0px;
  animation-name: slidein;
  -o-animation-name: slidein;
  -moz-animation-name: slidein;
  -webkit-animation-name: slidein;
  animation-duration: 0.5s;
  -o-animation-duration: 0.5s;
  -moz-animation-duration: 0.5s;
  -webkit-animation-duration: 0.5s;
  animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}

.sticky-menu {
  top: -100px;
  animation-name: slideout;
  -o-animation-name: slideout;
  -moz-animation-name: slideout;
  -webkit-animation-name: slideout;
  animation-duration: 0.5s;
  -o-animation-duration: 0.5s;
  -moz-animation-duration: 0.5s;
  -webkit-animation-duration: 0.5s;
  animation-fill-mode: forwards;
  -o-animation-fill-mode: forwards;
  -webkit-animation-fill-mode: forwards;
}

@keyframes slidein {
  from {
    top: 0px;
  }
  to {
    top: -100px;
  }
}
@keyframes slideout {
  from {
    top: -100px;
  }
  to {
    top: 0;
  }
}
.menu-wrapper {
  margin-right: 50px;
}

.menu-wrapper ul {
  display: flex;
  flex-direction: row;
  -ms-flex-direction: row;
  flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  list-style: none;
  height: 100%;
  margin: 0;
  justify-content: space-between;
}
.menu-wrapper ul li {
  font-size: 25px;
  width: 100%;
  text-align: center;
}
.menu-wrapper ul a {
  border-right: 2px solid #ff9999;
  width: 130px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: border 0.2s;
  box-sizing: border-box;
}
.menu-wrapper ul a:hover,
.menu-wrapper ul .active {
  border-bottom: 2px solid #ff9999;
}
.menu-wrapper ul a:last-child {
  border-right: none;
}

/*Размытый фон*/
.cover {
  position: absolute;
  top: -15%;
  left: -15%;
  background-image: inherit;
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-position: center;
  width: 130%;
  height: 130%;
  filter: blur(7px);
  box-shadow: 0 0 0 3000px inset rgba(0, 0, 0, 0.7);
  z-index: 2;
  overflow: hidden;
}

/*Секции*/
.sec {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  -ms-flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  z-index: 1;
}
.sec .content {
  position: relative;
  z-index: 3;
}

/*Приветсвие*/
