@font-face {
  font-family: 'fontello';
  src: url('./assets/ico/fontello/fontello.eot?4754082');
  src: url('./assets/ico/fontello/fontello.eot?4754082#iefix') format('embedded-opentype'),
       url('./assets/ico/fontello/fontello.woff2?4754082') format('woff2'),
       url('./assets/ico/fontello/fontello.woff?4754082') format('woff'),
       url('./assets/ico/fontello/fontello.ttf?4754082') format('truetype'),
       url('./assets/ico/fontello/fontello.svg?4754082#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
/* Chrome hack: SVG is rendered more smooth in Windozze. 100% magic, uncomment if you need it. */
/* Note, that will break hinting! In other OS-es font will be not as sharp as it could be */
